*, *::after, *::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none;
    list-style: none;
    text-decoration: none;
    -webkit-tap-highlight-color: transparent;
}

:root {
    --mPadding: calc((100vw - 320px) / (1700 - 320) * (40 - 20) + 20px);
}

.hide {
    opacity: 0;
    transform: scale(0);
}

.disabled {
    opacity: 0.5;
    pointer-events: none;
}

#root {
    display: flex;
    width: 100vw;
    /*min-width: 1300px;*/
    height: 100vh;
    background-image: url("img/home.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.card-field {
    display: flex;
    width: 40%;
    height: 100%;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    padding: var(--mPadding);
    gap: var(--mPadding);
    /*background: rgba(0, 0, 0, 0.5);*/
}

.card {
    display: flex;
    width: 45%;
    flex-shrink: 0;
    aspect-ratio: 1/1.3;
    /*height: 40%;*/
    border-radius: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: left bottom;
    transition: 0.4s;
    cursor: pointer;
    border: 2px solid aliceblue;
}

.card:hover {
    transform: scale(1.05);
    box-shadow: 0 3px 7px #ddd;
}

.card.active {
    /*box-shadow: 0 3px 7px purple;*/
    box-shadow: 0 0 8px 4px purple;
    border-color: purple;
}

.card:nth-child(1) {
    background-image: url("img/Ancients/Azathoth.jpg");
}

.card:nth-child(2) {
    background-image: url("img/Ancients/Cthulthu.jpg");
}

.card:nth-child(3) {
    background-image: url("img/Ancients/IogSothoth.jpg");
}

.card:nth-child(4) {
    background-image: url("img/Ancients/ShubNiggurath.jpg");
}


.buttons-field {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 30px;
    width: 20%;
    height: 100%;
    padding: 0 var(--mPadding);
}

.button {
    font-size: calc((100vw - 320px) / (1980 - 320) * (20 - 16) + 16px);
    font-weight: bold;
    width: 100%;
    max-width: 300px;
    border: 2px solid aliceblue;
    box-sizing: border-box;
    color: aliceblue;;
    padding: 10px 0;
    text-align: center;
    border-radius: 4px;
    cursor: pointer;
    background: rgba(150, 154, 154, 0.2);
    transition: .4s;
}

.button:hover {
    transform: scale(1.05);
    background: rgba(128, 0, 128, 0.5);
    box-shadow: 0 2px 6px #ddd;
}

.button.active {
    background: purple;
}

.game-field {
    display: flex;
    flex-direction: column;
    width: 40%;
    height: 100%;
    align-items: center;
    justify-content: center;
    position: relative;
    padding: 0 var(--mPadding);
}

.play-field {
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transform: scale(0);
    pointer-events: none;
    transition: 0.6s;
    padding: var(--mPadding) 0;
}

.play-field.active {
    opacity: 1;
    transform: scale(1);
    pointer-events: all;
}

.stages {
    display: flex;
    flex-direction: column;
    gap: 15px;
    /*width: 40%;*/
    padding: var(--mPadding);
}

.stage {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    gap: 15px;
    color: white;
    font-size: 20px;
    opacity: 0.4;
    transition: 0.6s;
}

.rounds {
    display: flex;
    gap: 40px;
}

.rounds .round {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    aspect-ratio: 1/1;
    border-radius: 50%;
    transition: 0.6s;
}

.stage.active .round {
    border: 2px solid white;
}

.rounds .round:nth-child(1) {
    background: rgba(0, 127, 21, 0.8);
}

.rounds .round:nth-child(2) {
    background: rgba(165, 42, 45, 0.8);
}

.rounds .round:nth-child(3) {
    background: rgba(51, 37, 192, 0.8);
}

.cards {
    display: flex;
    width: 100%;
    /*height: 100%;*/
    gap: var(--mPadding);
    padding: var(--mPadding);
}

.current-card,
.press-card {
    display: flex;
    width: 45%;
    aspect-ratio: 1/1.3;
    /*height: 100%;*/
    /*aspect-ratio: 1/1.5;*/
    border-radius: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    transition: 0.4s;
}

.press-card {
    cursor: pointer;
    background-image: url("img/mythicCardBackground.jpg");
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 22px;
    font-weight: bold;
    color: white;

}

.press-card:hover {
    box-shadow: 0 3px 7px #ddd;
}

.press-card:active {
    transform: scale(0.98);
}

.current-card {
    background-blend-mode: multiply;
    border: 1px solid #ddd;
    opacity: 0.4;
}

.off {
    pointer-events: none;
    box-shadow: 0 0 8px 4px white
}

@media (max-width: 767px) {
    .current-card,
    .press-card,
    .card {
        width: 65%;
    }

    .cards {
        flex-direction: column;
        align-items: center;
    }
}

.current-card.active,
.stage.active {
    opacity: 1;
}


